@import url(https://fonts.googleapis.com/css?family=Spartan&display=swap);
@import url(https://fonts.googleapis.com/css?family=Spartan&display=swap);
@import url(https://fonts.googleapis.com/css?family=Poppins);
$form-bg: #f8f4e5;
$form-border: #f45702;
$form-shadow: #dfae4b;
$form-focus: #6bd4b1;
$font-size: 12pt;
$font-face: 'Spartan';
$font-color: #2A293E;
* {
  margin: 0;
  padding: 0;
}

body {
  background: url("https://s3-us-west-2.amazonaws.com/s.cdpn.io/38816/image-from-rawpixel-id-2044837-jpeg.jpg") center center no-repeat;
  background-size: cover;
  width: 100vw;
  height: 100vh;
  display: grid;
  align-items: center;
  justify-items: center;
  font-size: $font-size;
  font-family: $font-face;
  color: $font-color;
}

#form-atendimentos {
  margin-top: 80px;
}

h2 {
  font-family: $font-face;
  margin: 30px 0;
}

label,
input,
textarea {
  display: block;
  width: 100%;
  font-size: $font-size;
  line-height: $font-size * 2;
  font-family: $font-face;
  box-sizing: border-box;
}

input {
  margin-bottom: $font-size;
}

h3 {
  font-weight: normal;
  font-size: 10pt;
  line-height: $font-size * 2;
  font-style: italic;
  margin: 0 0 .5em 0;
}

input,
textarea {
  border: none;
  border: 1px solid rgba(0, 0, 0, .1);
  border-radius: 2px;
  background: $form-bg;
  padding-left: 5px;
  outline: none
}

input:focus,
textarea:focus {
  border: 1px solid $form-focus;
}

textarea {
  resize: none;
}

input[type='submit'] {
  display: block;
  float: right;
  line-height: $font-size * 2;
  padding: 0 20px;
  border: none;
  background: $form-border;
  color: white;
  letter-spacing: 2px;
  transition: .2s all ease-in-out;
  border-bottom: 2px solid transparent;
  outline: none;
  cursor: pointer;
}

input[type='submit']:hover {
  background: inherit;
  color: $form-border;
  border-bottom: 2px solid $form-border;
}

input:-webkit-autofill,
input:-webkit-autofill:hover,
input:-webkit-autofill:focus,
textarea:-webkit-autofill,
textarea:-webkit-autofill:hover,
textarea:-webkit-autofill:focus {
  border: 1px solid $form-focus;
  -webkit-text-fill-color: $font-color;
  box-shadow: 0 0 0px 1000px $form-bg inset;
  -webkit-transition: background-color 5000s ease-in-out 0s;
  transition: background-color 5000s ease-in-out 0s;
}

select {
  padding: 2px;
  background: #fff;
  border: 1px solid #f3f3f3;
  box-shadow: 0 0 3px black;
  margin-left: 10px;
}

p.sucess,
p.failed {
  text-align: center;
}

p.sucess {
  color: #00ff00;
}

p.failed {
  color: #ff0000;
}
