#navbar {
  position: absolute;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
  display: flex;
  width: 320px;
  justify-content: space-around;
  margin: 20px 0;
  z-index: 999;
}

a {
  margin: 0 5px;
  padding: 10px;
  background: #f45702;
  color: #fff;
  text-decoration: none;
  cursor: pointer;
  letter-spacing: 2px;
  transition: .2s all ease-in-out;
  justify-content: center;
  align-items: center;
