/* BASIC */

html {
  background-color: #56baed;
}

body {
  font-family: "Poppins", sans-serif;
  height: 100vh;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: top center;
}


/* STRUCTURE */

.wrapper {
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: center;
  width: 100%;
  min-height: 100%;
  height: 100vh;
  padding: 20px;
}

#formContent {
  border-radius: 10px 10px 10px 10px;
  background: rgba(0, 0, 0, 0.5);
  padding: 30px;
  width: 90%;
  max-width: 450px;
  position: relative;
  padding: 0px;
  box-shadow: 0 30px 60px 0 rgba(0, 0, 0, 0.3);
  text-align: center;
}

#formContent h1 {
  color: #a3a3a3;
  padding: 20px 0;
}

#formContent p {
  font-weight: 600;
  color: #DC2026;
  padding-bottom: 20px;
}

#formFooter {
  background-color: #f6f6f6;
  border-top: 1px solid #dce8f1;
  padding: 25px;
  text-align: center;
  border-radius: 0 0 10px 10px;
}


/* TABS */

#formContent h2.inactive {
  color: #cccccc;
}

#formContent h2.active {
  color: #0d0d0d;
  border-bottom: 2px solid #5fbae9;
}


/* FORM TYPOGRAPHY*/

#formContent input[type=button],
#formContent input[type=submit],
#formContent input[type=reset] {
  background-color: #56baed;
  border: none;
  width: 85%;
  color: white;
  padding: 15px 80px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  text-transform: uppercase;
  font-size: 13px;
  box-shadow: 0 10px 30px 0 rgba(95, 186, 233, 0.4);
  border-radius: 5px 5px 5px 5px;
  margin: 5px 30px 10px 30px;
  transition: all 0.3s ease-in-out;
}

#formContent input[type=button]:hover,
#formContent input[type=submit]:hover,
#formContent input[type=reset]:hover {
  background-color: #39ace7;
}

#formContent input[type=button]:active,
#formContent input[type=submit]:active,
#formContent input[type=reset]:active {
  transform: scale(0.95);
}

#formContent input[type=text],
#formContent input[type=password] {
  background-color: rgba(10, 10, 10, 0.3);
  border: none;
  color: #f3f3f3;
  padding: 15px 32px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  margin: 5px;
  width: 85%;
  transition: all 0.5s ease-in-out;
  border-radius: 5px 5px 5px 5px;
}

#formContent input[type=text]:focus,
#formContent input[type=password]:focus {
  background-color: rgba(0, 0, 0, .5);
}

#formContent input[type=text]::-webkit-input-placeholder, #formContent input[type=password]::-webkit-input-placeholder {
  color: #a3a3a3;
}

#formContent input[type=text]:-ms-input-placeholder, #formContent input[type=password]:-ms-input-placeholder {
  color: #a3a3a3;
}

#formContent input[type=text]::-ms-input-placeholder, #formContent input[type=password]::-ms-input-placeholder {
  color: #a3a3a3;
}

#formContent input[type=text]::placeholder,
#formContent input[type=password]::placeholder {
  color: #a3a3a3;
}


/* ANIMATIONS */


/* Simple CSS3 Fade-in-down Animation */

.fadeInDown {
  -webkit-animation-name: fadeInDown;
  animation-name: fadeInDown;
  -webkit-animation-duration: 1s;
  animation-duration: 1s;
  -webkit-animation-fill-mode: both;
  animation-fill-mode: both;
}

@-webkit-keyframes fadeInDown {
  0% {
    opacity: 0;
    transform: translate3d(0, -100%, 0);
  }
  100% {
    opacity: 1;
    transform: none;
  }
}

@keyframes fadeInDown {
  0% {
    opacity: 0;
    transform: translate3d(0, -100%, 0);
  }
  100% {
    opacity: 1;
    transform: none;
  }
}


/* Simple CSS3 Fade-in Animation */

@-webkit-keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

.fadeIn {
  opacity: 0;
  -webkit-animation: fadeIn ease-in 1;
  animation: fadeIn ease-in 1;
  -webkit-animation-fill-mode: forwards;
  animation-fill-mode: forwards;
  -webkit-animation-duration: 1s;
  animation-duration: 1s;
}

.fadeIn.first {
  -webkit-animation-delay: 0.4s;
  animation-delay: 0.4s;
}

.fadeIn.second {
  -webkit-animation-delay: 0.6s;
  animation-delay: 0.6s;
}

.fadeIn.third {
  -webkit-animation-delay: 0.8s;
  animation-delay: 0.8s;
}

.fadeIn.fourth {
  -webkit-animation-delay: 1s;
  animation-delay: 1s;
}


/* Simple CSS3 Fade-in Animation */

.underlineHover:after {
  display: block;
  left: 0;
  bottom: -10px;
  width: 0;
  height: 2px;
  background-color: #56baed;
  content: "";
  transition: width 0.2s;
}

.underlineHover:hover {
  color: #0d0d0d;
}

.underlineHover:hover:after {
  width: 100%;
}


/* OTHERS */

*:focus {
  outline: none;
}

#icon {
  width: 60%;
}

